import { asyncRoutes, constantRoutes } from '@/router'

/**
 * Use meta.role to determine if the current user has permission
 * @param menus
 * @param route
 */
function hasPermission(menus, route) {
  return menus.some(menu => {
    return menu.MenuUrl.indexOf(route.path) != -1
  })
}

/**
 * Filter asynchronous routing tables by recursion
 * @param routes asyncRoutes
 * @param menus
 */
export function filterAsyncRoutes(routes, menus) {
  const res = []

  routes.forEach(route => {
    const tmp = { ...route }
    if (route.path==="*"||hasPermission(menus, tmp)) {
      if (tmp.children) {
        tmp.children = filterAsyncRoutes(tmp.children, menus)
      }
      res.push(tmp)
    }
  })
  return res
}

const state = {
  routes: [],
  addRoutes: []
}

const mutations = {
  SET_ROUTES: (state, routes) => {
    state.addRoutes = routes
    state.routes = constantRoutes.concat(routes)
    console.log('addRoutes', routes)
    console.log('routes', state.routes)
  }
}

const actions = {
  generateRoutes({ commit }, menus) {
    let pageMenus=menus.filter((menu)=>menu.MenuType==1||menu.MenuType==2)
    return new Promise(resolve => {
      let accessedRoutes = filterAsyncRoutes(asyncRoutes, pageMenus)
      console.log('generateRoutes accessedRoutes', accessedRoutes)
      commit('SET_ROUTES', accessedRoutes)
      resolve(accessedRoutes)
    })
  }
}

export default {
  namespaced: true,
  state,
  mutations,
  actions
}
