import axios from 'axios'
import { MessageBox, Message } from 'element-ui'
import store from '@/store'
import { getToken } from '@/utils/auth'
// const base = process.env.NODE_ENV=="development"?process.env.VUE_APP_BASE_API:`http://${window.location.hostname}:9024`;
const base = process.env.NODE_ENV == "development" ? process.env.VUE_APP_BASE_API : `http://test.supervise.xhs-sz.com:9024`;
// create an axios instance
const service = axios.create({
  baseURL: base, // url = base url + request url
  // withCredentials: true, // send cookies when cross-domain requests
  timeout: 5000 // request timeout
})

// request interceptor
service.interceptors.request.use(
  config => {
    // do something before request is sent

    if (store.getters.token) {
      // let each request carry token
      // ['X-Token'] is a custom headers key
      // please modify it according to the actual situation
      config.headers['X-Token'] = getToken()
      config.headers['Authorization'] = 'Bearer ' + getToken()
      // config.headers['Content-Type'] = "application/x-www-form-urlencoded"
      // config.headers["Content-Type"] = "application/json;charset=utf-8"
    }
    // // //post
    // if (config.method == "post" && config.data.body) {
    //   config.headers["Content-Type"] = "application/json;charset=utf-8"
    // }
    return config
  },
  error => {
    // do something with request error
    console.log(error) // for debug
    return Promise.reject(error)
  }
)

// response interceptor
service.interceptors.response.use(
  /**
   * If you want to get http information such as headers or status
   * Please return  response => response
  */

  /**
   * Determine the request status by custom code
   * Here is just an example
   * You can also judge the status by HTTP Status Code
   */
  response => {
    const res = response.data
    console.log('interceptors response:', response)

    // if the custom code is not 20000, it is judged as an error.
    if (res.Tag !== 1) {
      // Tag10
      Message({
        message: res.Message || 'Error',
        type: 'error',
        duration: 3 * 1000
      })

      // 50008: Illegal token; 50012: Other clients logged in; 50014: Token expired;
      if (res.Tag === 50008 || res.Tag === 50012 || res.Tag === 50014) {
        // to re-login
        MessageBox.confirm(', ,', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          store.dispatch('user/resetToken').then(() => {
            location.reload()
          })
        })
      }
      return Promise.reject(new Error(res.message || 'Error'))
    } else {
      return res
    }
  },
  error => {
    console.log('interceptors err:' + error) // for debug
    if (error.message == "Request failed with status code 401") {
      Message({
        message: "",
        type: 'error',
        duration: 3 * 1000
      })
    } else {
      Message({
        message: error.message,
        type: 'error',
        duration: 3 * 1000
      })
    }

    return Promise.reject(error)
  }
)

export const request = service;
export const baseURL = base;
