import { fetchList } from '@/api/projectType'

const state = {
  projectTypes: []
}

const mutations = {
  SET_PROJECT_TYPES: (state, projectTypes) => {
    state.projectTypes = projectTypes
  }
}

const actions = {
  // 
  fetchList({ commit }) {
    return new Promise((resolve, reject) => {
      fetchList().then(response => {
        const { Data } = response
        if (!Data|| Data.length <= 0) {
          reject('')
        }
        commit('SET_PROJECT_TYPES', Data)
        resolve(Data)
      }).catch(error => {
        reject(error)
      })
    })
  },
  delProjectType({ commit }) {
    commit('SET_PROJECT_TYPES', [])
  }
}

export default {
  namespaced: true,
  state,
  mutations,
  actions
}
