import {request} from "@/utils/request";

export function fetchPageList(params) {
  return request({
    url: "/User/GetPageList",
    method: "get",
    params
  });
}

export function saveUser(data) {
  return request({
    url: "/User/SaveForm",
    method: "post",
    data
  });
}

export function changeUser(data) {
  return request({
    url: "/User/ChangeUser",
    method: "post",
    data
  });
}

export function changePassword(data) {
  return request({
    url: "/User/ChangePassword",
    method: "post",
    data
  });
}

export function resetPassword(data) {
  return request({
    url: "/User/ResetPassword",
    method: "post",
    data
  });
}

export function deleteUser(data) {
  return request({
    url: "/User/DeleteForm",
    method: "post",
    data
  });
}

export function login(data) {
  return request({
    url: "/User/Login",
    method: "post",
    data
  });
}

export function getInfo(params) {
  return request({
    url: "/User/GetForm",
    method: "get",
    params
  });
}

export function logout(data) {
  return request({
    url: "/User/LoginOff",
    method: "post",
    data: data
  });
}
