import { login, logout, getInfo, changeUser } from "@/api/user";
import { baseURL } from "@/utils/request";
import { getFileUrl } from "@/api/file";
import { getToken, setToken, removeToken, getUserId, setUserId, removeUserId, setCompanyId, getCompanyId } from "@/utils/auth";
import router, { resetRouter } from "@/router";

const state = {
  token: getToken(),
  UserId: getUserId(),
  UserName: "",
  RealName: "",
  Gender: "",
  Birthday: "",
  Mobile: "",
  Email: "",
  Portrait: "",
  avatar: "",
  companyId: getCompanyId(),
  introduction: "",
  roles: []
};

const mutations = {
  SET_TOKEN: (state, token) => {
    state.token = token;
  },
  SET_USER_ID: (state, UserId) => {
    state.UserId = UserId;
  },
  SET_INTRODUCTION: (state, introduction) => {
    state.introduction = introduction;
  },
  SET_USER_NAME: (state, UserName) => {
    state.UserName = UserName;
  },
  SET_REAL_NAME: (state, RealName) => {
    state.RealName = RealName;
  },
  SET_GENDER: (state, Gender) => {
    state.Gender = Gender;
  },
  SET_BIRTHDAY: (state, Birthday) => {
    state.Birthday = Birthday;
  },
  SET_MOBILE: (state, Mobile) => {
    state.Mobile = Mobile;
  },
  SET_EMAIL: (state, Email) => {
    state.Email = Email;
  },
  SET_PORTRAIT: (state, Portrait) => {
    state.Portrait = Portrait;
  },
  SET_AVATAR: (state, avatar) => {
    state.avatar = avatar;
  },
  SET_COMPANY_ID: (state, companyId) => {
    state.companyId = companyId;
  },
  SET_ROLES: (state, roles) => {
    state.roles = roles;
  }
};

const actions = {
  // 
  login({ commit }, userInfo) {
    const { UserName, Password } = userInfo;
    return new Promise((resolve, reject) => {
      login({ UserName: UserName.trim(), Password: Password }).then(response => {
        // const Data  = {"token":"admin-token"}
        const { Data } = response;
        if (!Data) {
          reject(", ");
        }
        const { ApiToken, UserId, SuperviseCompanyId } = Data;
        commit("SET_TOKEN", ApiToken);
        commit("SET_USER_ID", UserId);
        commit("SET_COMPANY_ID", SuperviseCompanyId);
        setToken(ApiToken);
        setUserId(UserId);
        setCompanyId(SuperviseCompanyId);
        resolve();
      }).catch(error => {
        console.log("login error", error);
        reject(error);
      });
    });
  },

  // 
  getInfo({ commit, state }) {
    return new Promise((resolve, reject) => {
      getInfo({ Id: state.UserId }).then(response => {
        const { Data } = response;

        if (!Data) {
          reject(", .");
        }

        const { UserName, RealName, Gender, Birthday, Mobile, Email, Portrait, Remark } = Data;
        if (Portrait) {
          getFileUrl({ IdS: Portrait }).then((response) => {
            let relativeUrl = response.Data[0].FileId;
            relativeUrl = relativeUrl.replace(/\\/g, "/")
            commit("SET_AVATAR", baseURL + "/" + relativeUrl);
          });
        }
        commit("SET_USER_NAME", UserName);
        commit("SET_REAL_NAME", RealName);
        commit("SET_GENDER", Gender);
        commit("SET_BIRTHDAY", Birthday);
        commit("SET_MOBILE", Mobile);
        commit("SET_EMAIL", Email);
        commit("SET_PORTRAIT", Portrait);
        // commit('SET_AVATAR', "https://ss1.baidu.com/9vo3dSag_xI4khGko9WTAnF6hhy/zhidao/wh%3D450%2C600/sign=a587b23df11f3a295a9dddcaac159007/500fd9f9d72a60590cfef2f92934349b023bba62.jpg")
        commit("SET_INTRODUCTION", Remark);
        resolve(Data);
      }).catch(error => {
        reject(error);
      });
    });
  },

  // 
  changeUser({ commit, state }, userInfo) {
    return new Promise((resolve, reject) => {
      changeUser(Object.assign({ Id: state.UserId }, userInfo)).then(response => {
        // const { Data } = response

        // if (!Data) {
        //   reject(', .')
        // }

        // const { RealName,Gender,Birthday,Mobile,Email, Portrait, Remark } = Data

        // commit('SET_REAL_NAME', RealName)
        // commit("SET_GENDER", Gender);
        // commit("SET_BIRTHDAY", Birthday);
        // commit("SET_MOBILE", Mobile);
        commit("SET_EMAIL", userInfo.Email);
        // commit("SET_PORTRAIT", Portrait);
        // commit('SET_AVATAR', "https://ss1.baidu.com/9vo3dSag_xI4khGko9WTAnF6hhy/zhidao/wh%3D450%2C600/sign=a587b23df11f3a295a9dddcaac159007/500fd9f9d72a60590cfef2f92934349b023bba62.jpg")
        // commit('SET_INTRODUCTION', Remark)
        // resolve(Data)
        resolve();
      }).catch(error => {
        reject(error);
      });
    });
  },

  // user logout
  logout({ commit, state, dispatch }) {
    return new Promise((resolve, reject) => {
      logout({ token: state.token }).then(() => {
        commit("SET_TOKEN", "");
        commit("SET_ROLES", []);
        removeToken();
        removeUserId();
        resetRouter();

        // reset visited views and cached views
        // to fixed https://github.com/PanJiaChen/vue-element-admin/issues/2485
        dispatch("tagsView/delAllViews", null, { root: true });

        dispatch("menu/delMenu", null, { root: true });

        resolve();
      }).catch(error => {
        reject(error);
      });
    });
  },

  // remove token
  resetToken({ commit }) {
    return new Promise(resolve => {
      commit("SET_TOKEN", "");
      commit("SET_ROLES", []);
      removeToken();
      removeUserId();
      resolve();
    });
  },

  // dynamically modify permissions
  async changeRoles({ commit, dispatch }, role) {
    const token = role + "-token";

    commit("SET_TOKEN", token);
    setToken(token);

    const { roles } = await dispatch("getInfo");

    resetRouter();

    // generate accessible routes map based on roles
    const accessRoutes = await dispatch("permission/generateRoutes", roles, { root: true });
    // dynamically add accessible routes
    router.addRoutes(accessRoutes);

    // reset visited views and cached views
    dispatch("tagsView/delAllViews", null, { root: true });
  }
};

export default {
  namespaced: true,
  state,
  mutations,
  actions
};
