import {request} from "@/utils/request";

export function fetchList(query) {
  return request({
    url: "/Menu/GetList",
    method: "get",
    params: query
  });
}

export function fetchListByToken(params) {
  return request({
    url: "/Menu/GetListByToken",
    method: "get",
    params
  });
}

export function fetchMenu(params) {
  return request({
    url: "/Menu/GetForm",
    method: "get",
    params
  });
}

export function saveMenu(data) {
  return request({
    url: "/Menu/SaveForm",
    method: "post",
    data
  });
}

export function deleteMenu(data) {
  return request({
    url: "/Menu/DeleteForm",
    method: "post",
    data
  });
}
