import router from './router'
import store from './store'
import { Message } from 'element-ui'
import NProgress from 'nprogress' // progress bar
import 'nprogress/nprogress.css' // progress bar style
import { getToken } from '@/utils/auth' // get token from cookie
import getPageTitle from '@/utils/get-page-title'

NProgress.configure({ showSpinner: false }) // NProgress Configuration

const whiteList = ['/login', '/auth-redirect'] // no redirect whitelist

router.beforeEach(async (to, from, next) => {
  // 
  NProgress.start()

  // 
  document.title = getPageTitle(to.meta.title)

  // 
  const hasToken = getToken()
  console.log('hasToken', hasToken)
  if (hasToken) {
    console.log('to.path', to.path)
    if (to.path === '/login') {
      // , 
      next({ path: '/' })
      NProgress.done() // hack: https://github.com/PanJiaChen/vue-element-admin/pull/2939
    } else {
      //getInfo
      const hasMenus = store.getters.menus && store.getters.menus.length > 0
      console.log('hasMenus', hasMenus)
      if (hasMenus) {
        next()
      } else {
        try {
          // 
          // note: roles must be a object array! such as: ['admin'] or ,['developer','editor']
          const userInfo = await store.dispatch('user/getInfo')
          const role = await store.dispatch('role/getRole', {
            id: userInfo.RoleIds
          })
          const menus = await store.dispatch('menu/fetchList')
          // generate accessible routes map based on roles
          const accessRoutes = await store.dispatch('permission/generateRoutes', menus)
          // dynamically add accessible routes
          router.addRoutes(accessRoutes)
          const dicts = await store.dispatch('dict/fetchList')
          const projectTypes = await store.dispatch('projectType/fetchList')

          // hack method to ensure that addRoutes is complete
          // set the replace: true, so the navigation will not leave a history record
          next({ ...to, replace: true })
        } catch (error) {
          //  token 
          await store.dispatch('user/resetToken')
          Message.error(error || 'Has Error')
          next(`/login?redirect=${to.path}`)
          NProgress.done()
        }
      }
    }
  } else {
    /* token */

    if (whiteList.indexOf(to.path) !== -1) {
      // , 
      next()
    } else {
      // .
      next(`/login?redirect=${to.path}`)
      NProgress.done()
    }
  }
})

router.afterEach(() => {
  // 
  NProgress.done()
})
