import { fetchList } from '@/api/dict'

const state = {
  dicts: []
}

const mutations = {
  SET_DICTS: (state, dicts) => {
    state.dicts = dicts
  }
}

const actions = {
  // 
  fetchList({ commit }) {
    return new Promise((resolve, reject) => {
      fetchList().then(response => {
        const { Data } = response
        if (!Data|| Data.length <= 0) {
          reject('')
        }
        commit('SET_DICTS', Data)
        resolve(Data)
      }).catch(error => {
        reject(error)
      })
    })
  },
  delDict({ commit }) {
    commit('SET_DICTS', [])
  }
}

export default {
  namespaced: true,
  state,
  mutations,
  actions
}
