import { fetchListByToken } from '@/api/menu'
import { getToken} from '@/utils/auth'

const state = {
  menus: []
}

const mutations = {
  SET_MENUS: (state, menus) => {
    state.menus = menus
  }
}

const actions = {
  // 
  fetchList({ commit, state }) {
    return new Promise((resolve, reject) => {
      fetchListByToken({Token:getToken()}).then(response => {
        const { Data } = response
        if (!Data|| Data.length <= 0) {
          reject('')
        }
        commit('SET_MENUS', Data)
        resolve(Data)
      }).catch(error => {
        reject(error)
      })
    })
  },
  delMenu({ commit, state }) {
    commit('SET_MENUS', [])
  }
}

export default {
  namespaced: true,
  state,
  mutations,
  actions
}
