import { getRole } from '@/api/role'
import { param } from '@/utils'

const state = {
  Id:"",
  roleName:"",
  RoleSort:"",
  RoleStatus: '',
  MenuIds: '',
  Remark: '',
}

const mutations = {
  SET_ID: (state, Id) => {
    state.Id = Id
  },
  SET_ROLE_NAME: (state, roleName) => {
    state.roleName = roleName
  },
  SET_ROLE_SORT: (state, RoleSort) => {
    state.RoleSort = RoleSort
  },
  SET_ROLE_STATUS: (state, RoleStatus) => {
    state.RoleStatus = RoleStatus
  },
  SET_MENU_IDS: (state, MenuIds) => {
    state.MenuIds = MenuIds
  },
  SET_REMARK: (state, Remark) => {
    state.Remark = Remark
  }
}

const actions = {
  // 
  getRole({ commit },params) {
    return new Promise((resolve, reject) => {
      getRole(params).then(response => {
        const { Data } = response

        if (!Data) {
          reject(', .')
        }

        const { Id,RoleName, RoleSort, RoleStatus,Remark,MenuIds} = Data

        commit('SET_ID', Id)
        commit('SET_ROLE_NAME', RoleName)
        commit('SET_ROLE_SORT', RoleSort)
        commit('SET_ROLE_STATUS', RoleStatus)
        commit('SET_MENU_IDS', MenuIds)
        commit('SET_REMARK', Remark)
        resolve(Data)
      }).catch(error => {
        reject(error)
      })
    })
  },
}

export default {
  namespaced: true,
  state,
  mutations,
  actions
}
